    .box {
        /* grid-auto-flow: row;
        grid-row-end: auto; */
        display: flex;
        flex-direction: row;
    }

    .div1 {
        order: 1;
        /* border: 1px solid red; */
        justify-content: center;
    }

    .div2 {
        order: -1;
    }

}