.scrollLinkMobile {
    display: block;
    cursor: pointer;
    transition: 0.2s;
   

}

.scrollLinkMobile:hover {
    transition: 0.2s;
    color: "red";
    text-decoration: "none";
}

.scrollLink {
    transition: 0.2s;
    cursor: pointer;
    justify-content: center;
    

}

.scrollLink:hover {
    transition: 0.2s;
    color: "#d70000";
    text-decoration: "none";
}